.container {
    position: relative;
    height: 600px;
    width: 600px;
    filter: url(#filter);
    transform: rotate(0deg);
}

.circle {
    position: absolute;
    background: #4893dc;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    filter: blur(15px);
    box-shadow: 5px -15px 15px rgba(255, 255, 255, 0.05),
    inset -5px -15px 15px rgba(0, 0, 0, 0.3),
    inset -5px 15px 15px rgba(255, 255, 255, 0.05);
}

.svg{
    display: none;
}

