.container {
    margin: 50px 0;
}

.title {
    text-transform: uppercase;
    font-size: 60px;
    margin: 0;
    box-sizing: border-box;
    position: relative;
    line-height: 1.5em;
    color: transparent;
    -webkit-text-stroke: 2px #03a9f4;
}

.title > div {
    position: absolute;
    top: 0;
    left: 0;
    color: #03a9f4;
    clip-path: polygon(0% 50%, 6% 40%, 12% 32%, 18% 30%, 24% 32%, 32% 40%,
    36% 50%, 42% 60%, 48% 68%, 54% 70%, 60% 68%, 66% 60%, 72% 50%, 78% 40%,
    84% 32%, 90% 30%, 96% 32%,100% 50%, 100% 100%, 0% 100%);
    animation: animate 3s linear infinite;
}

@keyframes animate {
    0%,100% {
        clip-path: polygon(0% 50%, 6% 40%, 12% 32%, 18% 30%, 24% 32%, 32% 40%,
        36% 50%, 42% 60%, 48% 68%, 54% 70%, 60% 68%, 66% 60%, 72% 50%, 78% 40%,
        84% 32%, 90% 30%, 96% 32%,100% 38%, 100% 100%, 0% 100%);
    }
    50% {
        clip-path: polygon(0% 50%, 6% 60%, 12% 68%, 18% 70%, 24% 68%, 32% 60%,
        36% 50%, 42% 40%, 48% 32%, 54% 30%, 60% 32%, 66% 40%, 72% 50%, 78% 60%,
        84% 68%, 90% 70%, 96% 68%,100% 62%, 100% 100%, 0% 100%);
    }
}
