.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background: #ffb33c;

}

.title {
    padding: 0;
    color: #ee3556;
    font-size: 50px;
    margin: 50px 0;
}

.text {
    max-width: 70%;
    line-height: 1.5em;
    font-size: 20px;
    text-align: justify;
    color: #b51f4a;
}